export default    function newBall(event){//
		let div=document.createElement('div');
		div.className='ballFather';
		document.body.appendChild(div);

		let sonDiv=document.createElement('div');
		sonDiv.className='ballSon';
		div.appendChild(sonDiv);

		// console.log(event.target.getBoundingClientRect().top);
		let top=event.clientY;// top
		let left=event.clientX;// left
		div.style.left=left+'px';
		div.style.top=top+'px';

		let y = (window.innerHeight - top);// 
		// div.style.webkitTransform = `translateY(${y}px)`;
		div.style.transform = `translateY(${y}px)`;


		// sonDiv.style.webkitTransform = `translateX(-${left-10}px)`;
		sonDiv.style.transform = `translateX(-${left-10}px)`;
		setTimeout(()=>{
			document.body.removeChild(div)
		},600)
		// 

		
			// document.querySelector('.sc').classList class sc    
			// document.querySelector('.shopcars').classList.add("animation");
			// setTimeout(()=>{
			// 	document.querySelector('.shopcars').classList.remove("animation");
			// },400)

		// js    
	}




